workbox_routing_registerRoute(/\.(?:png|gif|jpg|jpeg|svg|webp|ico)$/, new workbox_strategies_CacheFirst({ "cacheName":"images-cache", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 100, maxAgeSeconds: 2592000 })] }), 'GET');
